import React, { useState } from "react";
import { Link } from "gatsby";
import Cookies from "cookies-js";

const CookieBanner = () => {
  const [acceptedCookie, setAcceptedCookie] = useState(
    Cookies("acceptCookie") === undefined ? false : true
  );

  const handleAcceptCookies = () => {
    Cookies.set("acceptCookie", true, { expires: Infinity });
    setAcceptedCookie(true);
  };

  if (acceptedCookie) {
    return null;
  }

  return (
    <div className="fixed-bottom cookie-banner text-center d-flex align-items-center justify-content-between flex-column flex-md-row">
      <span>
        Vi anvnder cookies - genom att anvnda denna sida eller stnga detta
        meddelande accepterar du vr cookiepolicy.{" "}
        <Link to="/cookies">Ls mer</Link>
      </span>
      <button
        className="btn btn-primary float-right mt-2"
        onClick={handleAcceptCookies}
      >
        Ok
      </button>
    </div>
  );
};

export default CookieBanner;
